import React from 'react';
import ReactDOM from 'react-dom';
import {Header} from './Header/Header';
import {SliderCollage} from './Slider/SliderCollage';
import {SliderData} from './sliderData';



//header
let navbarList = ['Projects', 'About', 'Contact'];
ReactDOM.render(
    <Header list = {navbarList} name = "AMRITPAL CHERA" TitleHref = "/"/>,
    document.getElementById('header')
);


ReactDOM.render(
  <SliderCollage title = "Projects" sliderData = {SliderData}></SliderCollage>,
  document.getElementById('projectsPage')
);



