import React from 'react';
import {Title} from './Title';
import {Writing} from './Writing';
import '../styles.css';
import PropTypes from 'prop-types';

//props.sliderImages
//props.text
//props.title


export class Slider extends React.Component{

    constructor(props){
        super(props);
        this.state  = {index: 0, path: "/static/images/projectsSlider/", colorLeft: 'white', colorRight: 'white'};
        this.handleLeftClick = this.handleLeftClick.bind(this);
        this.handleRightClick = this.handleRightClick.bind(this);

    }

    handleLeftClick(){
        let _index = this.state.index;
        
        if (this.state.index > 0){
            _index -= 1;
        }
        else{
            this.setState({colorLeft: 'red'});
            setTimeout(()=>{
                this.setState({colorLeft: 'white'});
            },
            200
            );
        }
            

        this.setState ({index:_index});
        //console.log(this.state.index);
    }

    handleRightClick(){
        let _index = this.state.index;
        if (this.state.index < (this.props.imgs.length - 1)){
            _index += 1;
        }
        else{
            this.setState({colorRight: 'red'});
            //console.log('hello '+this.state.colorRight);
            
            setTimeout(()=>{
                this.setState({colorRight: 'white'});
            },
            200
            );
        }
        //console.log(this.state.color);
        this.setState ({index:_index});
       // console.log(this.state.index);
    }

    render(){
        let img = this.props.imgs[this.state.index];
       // console.log(img);
        img = this.state.path+img;
        const slider = (
            <div className = "sliderGridChild">
                <div id = "titles">
                    <Title  name  ={this.props.title}></Title>
                    <Title subTitle = {this.props.subtitleLink} linkLabel = {this.props.linkLabel}></Title>
                </div>
                
                <div className = "sliderContent">
                    <div className = "sliderWindow">
                        <button className = "sliderLeftBtn" onClick = {this.handleLeftClick} style={{color: this.state.colorLeft}}>&#10094;</button>
                        <button className = "sliderRightBtn" onClick = {this.handleRightClick} style={{color: this.state.colorRight}}>&#10095;</button>
                        <img className = "sliderImg" src = {img} alt = ""></img>
                    </div>
                    <Writing  text = {this.props.text} ></Writing>
                </div>
                
            </div>
        );
        return(slider);
    }
    

}

Slider.propTypes = {
    imgs : PropTypes.array.isRequired
};