import React from 'react';
import '../styles.css'

export const Writing = (props)=>{
    let text = null;
    let style = null;
    
    if (props.ListView){
        if (props.ordered){
            style = 'decimal';
        }
        else if (props.bullets){
            style = "initial";
        }
        else style = "none"
        
        console.log(style);
        text = (
            props.text.map((e)=>{
                return <li  className = "textSubpageListItem" style  = {{listStyleType: style, marginTop: '2vh'}} key={e}>{e}</li>
            })
        );
        
    }
    else text = props.text;
    return (
        <p className = 'sliderText'>{text} </p>
    );
}