import React from 'react';
import PropTypes from 'prop-types';
import '../styles.css'

export const NavBar = (props)=>{
    const items = props.items.map((e)=>{
        return (
        <li className = "navlistItems" key = {e}>
            <a style ={{color: 'white'}}href={"/"+e.toLowerCase()}>{e}</a>
        </li>);
    });
    return(<ul className="navlist">{items}</ul>);
};

NavBar.propTypes ={
    items: PropTypes.array.isRequired
};