import React from 'react';
import {Slider} from './Slider';

export class SliderCollage extends React.Component{
    render(){
        let sections = [];
        for (var element in this.props.sliderData){
            //console.log(this.props.sliderData[element].link);
            sections.push(
            <Slider 
                imgs = {this.props.sliderData[element].imgs}
                title = {this.props.sliderData[element].title}
                text = {this.props.sliderData[element].text}
                subtitleLink = {this.props.sliderData[element].link}
                linkLabel = {this.props.sliderData[element].linkLabel}
            ></Slider>);
        }
        let toReturn = (
            sections.map((e, index)=>{
                return(
                    <div key = {index} className = "sliderGrid">
                        {e}
                    </div>
                );
            })
        );
        return(
            <div>
                 <h1 className = "titleSubpage">{this.props.title}</h1>
                <div id = "allSliders">
                    {toReturn}
                </div>
            </div>
           
        );
    }
}