import React from 'react';
import '../styles.css';

export const Title = (props)=>{
    let toReturn = null;
    let text = props.linkLabel;
    props.subTitle? toReturn = (<a rel = {"noopener noreferrer"} target="_blank" id ={props.subTitle} className = "slidersubtitle" href = {props.subTitle}>{text}</a>):
    toReturn = (<a rel = {"noopener noreferrer"} target="_blank" className= "sliderTitle" id = {props.name} href = {props.href}>{props.name}</a>);
    return (toReturn);
}